import { AbstractParameterControllerWidget, ControllerValidation } from '../controller.abstract';
import { shortcut } from '@engine/core';

@shortcut()
export class ParameterEmpty extends AbstractParameterControllerWidget {
    public static xtype = 'report.main.parameter_container.controller.empty';

    public preGetValidation(): ControllerValidation {
        return {
            result: true,
        };
    }

    public postGetValidation(): ControllerValidation {
        return { result: true };
    }

    public postValidateAndToast() {
        return true;
    }

    public reset() {}

    public setInteractValue() {}

    public async setControllerValue() {}

    public getValue() {
        return '';
    }

    public setStyle() {}
}
